<template>
	<view class="mask_wrap" :class="{active:shower.outer}" @click="maskClick" :style="{zIndex:zIndex,backgroundColor:maskBG}" @touchmove.prevent>
		<view class="mask_con" :class="{active:shower.inner,center:isCenter,bottom:isBottom,top:isTop,right:isRight,left:isLeft}" @click.stop="stop">
			<slot name="slot_mask"></slot>
		</view>
	</view>
</template>
<script setup>
import {ref,toRefs,computed} from "vue";
import {stop} from "@/utils/tools";
const shower = ref({
	outer:false,
	inner:false
})
const props = defineProps({
	maskBG:{
		type: String,
		default: 'rgba(0,0,0,.5)'
	},
	zIndex:{
		type: Number,
		default: 999
	},
	isBottom:{
		type: Boolean,
		default: false
	},
	isTop:{
		type: Boolean,
		default: false
	},
	isCenter:{
		type: Boolean,
		default: false
	},
	isRight:{
		type: Boolean,
		default: false
	},
	isLeft: {
		type: Boolean,
		default: false
	},
	isClickMaskHide:{
		type: Boolean,
		default: true
	}
})
const {maskBG,zIndex,isBottom,isTop,isCenter,isRight,isLeft,isClickMaskHide} = toRefs(props)
const emits = defineEmits(['clickMask'])
const maskShow = ()=>{
	shower.value.outer = true;
	setTimeout(()=>{shower.value.inner = true;},200)
}
const maskHide = ()=>{
	emits('clickMask')
	shower.value.inner = false;
	setTimeout(()=>{shower.value.outer = false;},200)
}
const maskClick = ()=>{
	if(!isClickMaskHide.value) return false;
	maskHide()
}
defineExpose({ maskShow,maskHide });
</script>
<style scoped lang="scss">
.mask_wrap{
	width: 100%;height: 100%;position: fixed;top: 0;left: 0;visibility: hidden;opacity: 0;transition: all .3s;backdrop-filter: blur(2px);z-index:999;
	&.active{visibility: visible;opacity: 1;}
	.mask_con{
		height: auto;transition: all .3s;visibility: hidden;z-index: 22;
		&.center{position: absolute;top: 50%;left: 50%;transform: translate(-50%,-60%);opacity: 0;}
		&.top{position: absolute;top: 0;left: 0;transform: translateY(-100%);}
		&.bottom{position: absolute;bottom: 0;left: 0;transform: translateY(100%);}
		&.right{position: absolute;top: 0;right: 0;transform: translateX(100%);}
		&.left{position: absolute;top: 0;left: 0;transform: translateX(-100%);}
		&.active{
			&.center{visibility: visible;opacity: 1;transform: translate(-50%,-50%);}
			&.top,&.bottom{visibility: visible;transform: translateY(0);}
			&.right,&.left{visibility: visible;transform: translateX(0);}
		}
	}
}
</style>